* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Karla, sans-serif;
  text-decoration: none;
  list-style: none;
}

header {
  padding: 2rem 0;
  position: fixed;
  top: 0;
  left: 0;
}

body {
  background: #131b23;
  margin: 2px 30px 0 20px;
}

.container {
  padding-top: 120px;
}

.container__title {
  margin-bottom: 30px;
  font-size: 25px;
}

.cart .product {
  background: #ffffff31;
  border-radius: 10px;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0 10px 0;
  padding: 20px 35px 20px 20px;
  display: flex;
}

.cart .product__image {
  width: 150px;
  margin: 0 5px 0 0;
}

.cart .product__price {
  color: #fff;
  font-size: 17px;
}

.cart .product__name {
  color: #fff;
  width: 180px;
  font-size: 18px;
  font-weight: 200;
}

.cart .product__delete {
  color: #f64d33;
  text-align: center;
  width: 100px;
  height: 38px;
  cursor: pointer;
  background: #fff;
  border: none;
  border-radius: 5px;
  font-weight: 200;
  box-shadow: 0 2px 4px #0003;
}

.cart .product__delete:hover {
  background: #d3d0d0;
}

.total__first {
  justify-content: flex-end;
  margin-top: 15px;
  margin-bottom: 15px;
  margin-left: 20px;
  font-weight: 200;
  display: flex;
}

.total__title {
  margin-right: 5px;
  font-size: 20px;
  font-weight: 100;
}

.total__total {
  color: #fff;
  font-weight: 100;
}

.check {
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.total__checkout {
  color: #fff;
  text-align: center;
  width: 300px;
  height: 38px;
  cursor: pointer;
  background: #f64d33;
  border: none;
  border-radius: 5px;
  margin-bottom: 20px;
  padding-top: 10px;
  font-weight: 200;
  box-shadow: 0 2px 4px #0003;
}

.total__checkout:hover {
  background: #fa614a;
}

